.home {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 15%;
  position: relative;

  a {
    font-size: 55px;
    font-weight: bold;
    letter-spacing: 3px;
    color: white;
    padding: 15px 0px;
    text-decoration: none;
    z-index: 1;

    &:hover {
      color: #e5384c;
    }
  }

  .title {
    width: 100%;
    color: #171d21;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    font-size: 200px;
    z-index: 0;
    text-transform: uppercase;
  }
}
