.pool-container {
  display: flex;
  width: 100%;
  height: 100%;

  .indicators {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 15%;

    & > div {
      color: white;
      display: flex;
      flex-direction: column;
      text-align: left;

      .percentage {
        font-weight: bold;
        font-size: 50px;
      }
    }
  }

  .buttons {
    display: flex;
    justify-content: space-around;
    width: 80%;
    padding-top: 40px;

    .button {
      height: 100px;
      width: 100px;
      background-color: #3d5575;
      border-radius: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      padding: 0px 5px 10px 5px;
      color: white;
      font-weight: bold;
      font-size: 15px;
      position: relative;
      cursor: pointer;

      transition: background-color 0.3s;

      &::after {
        content: "";
        height: 5px;
        width: 50%;
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        background: grey;
        border-radius: 2px;
      }

      &.active {
        &::after {
          background: rgb(6, 161, 6);
        }
      }

      &.warning {
        animation: warning 2s infinite;
      }

      @keyframes warning {
        0% {
          background-color: #3d5575;
        }
        50% {
          background-color: #e02d2d;
        }
        100% {
          background-color: #3d5575;
        }
      }
    }
  }
}

.pool {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .swim-pool-container {
    transition: background-color 1s;
    border-radius: 10px;
    position: relative;

    &::before {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      content: "";
      background: rgba(255, 255, 255, 0.5);
      opacity: 0;
      z-index: 10;

      transition: opacity 0.3s;
    }

    &::after {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      content: "Intrusion";
      background: #e02d2d;
      z-index: 20;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      text-transform: uppercase;
      font-size: 50px;
      letter-spacing: 5px;
      font-weight: bold;

      opacity: 0;
      @keyframes warningIntrusion {
        0% {
          opacity: 1;
        }
        50% {
          opacity: 0.5;
        }
        100% {
          opacity: 1;
        }
      }
    }
  }

  &.intrusion {
    .swim-pool-container::after {
      animation: warningIntrusion 2s infinite;
    }
  }

  .swim-pool-container-left,
  .swim-pool-container-right {
    transition: background-color 1s;
  }

  &.little-trouble {
    .swim-pool-container::before {
      opacity: 0.1;
    }
  }

  &.semi-trouble {
    .swim-pool-container::before {
      opacity: 0.5;
    }
  }

  &.almost-trouble {
    .swim-pool-container::before {
      opacity: 0.7;
    }
  }

  &.trouble {
    .swim-pool-container::before {
      opacity: 0.9;
    }
  }

  &.little-dirty {
    .swim-pool-container {
      background-color: #7eb6b5;
    }

    .swim-pool-container-left,
    .swim-pool-container-right {
      background-color: #379db0;
    }
  }

  &.semi-dirty {
    .swim-pool-container {
      background-color: #6ea279;
    }

    .swim-pool-container-left,
    .swim-pool-container-right {
      background-color: #3a8677;
    }
  }

  &.almost-dirty {
    .swim-pool-container {
      background-color: #619145;
    }

    .swim-pool-container-left,
    .swim-pool-container-right {
      background-color: #3c7245;
    }
  }

  &.dirty {
    .swim-pool-container {
      background-color: #548014;
    }

    .swim-pool-container-left,
    .swim-pool-container-right {
      background-color: #3e5d12;
    }
  }
}
.swim-pool-container {
  /* position: absolute;
  top: 50%;
  left: 50%; */
  position: relative;
  width: 80%;
  height: 80%;
  border: 15px solid #e9ebea;
  /* transform: translate(-50%, -50%); */
  z-index: 2;
  background-color: #8ac6e2;
}
.swim-pool-container-left {
  height: 100%;
  width: 10%;
  position: absolute;
  top: 0;
  z-index: 5;
  background: #35afdc;
  background-size: 50% 50%;
  background-repeat: no-repeat;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.swim-pool-container-right {
  width: 100%;
  height: 12%;
  background-color: #dbdad8;
  transform-origin: bottom;
  z-index: 5;
  background: #35afdc;
  background-size: 50% 50%;
  background-repeat: no-repeat;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.light {
  height: 30px;
  width: 30px;
  background: yellow;
  border-radius: 50%;
  filter: opacity(0.5);

  &.off {
    background: #6f6f6f;
  }
}

.tyre {
  border: 10px solid #e0a9b5;
  width: 10%;
  height: 20%;
  border-radius: 50%;
  position: absolute;
  box-shadow: 1px 1px 2px #147dbd, -1px -1px 2px #147dbd;
}
.tyre2 {
  left: 20%;
  top: 30%;
}
.tyre1 {
  left: 50%;
  top: 50%;
}
.stairs {
  z-index: 20;
  left: -10%;
  position: absolute;
  width: 20%;
  height: 12%;
  position: absolute;
  border: 3px solid #ababab;
  border-left: none;
  border-right: none;
  display: flex;
  justify-content: space-evenly;
  div {
    width: 2px;
    height: 100%;
    background-color: #ababab;
  }
}

.card-deck .card {
  min-width: 200px;
  min-height: 200px;
}

.border-top {
  border-top: 1px solid #e5e5e5;
}
.border-bottom {
  border-bottom: 1px solid #e5e5e5;
}

.box-shadow {
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);
}

/* ------ Default Style ---------- */
.gauge-container {
  padding: 10px;
  margin-bottom: auto;
  margin-top: auto;
}
.gauge-container > .gauge > .dial {
  stroke: rgb(47, 227, 255);
  stroke-width: 2;
  fill: rgba(0, 0, 0, 0);
}
.gauge-container > .gauge > .value {
  stroke: #03a9f4;
  stroke-width: 4;
  fill: rgba(0, 0, 0, 0);
}
.gauge-container > .gauge > .value-text {
  fill: rgb(47, 227, 255);
  font-family: sans-serif;
  font-weight: bold;
  font-size: 0.8em;
}

.bg-gradient {
  color: #ffffff !important;
  background: #fff;
  -webkit-transition: opacity 0.2s ease-out;
  -moz-transition: opacity 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
}

.card.bg-gradient > .gauge-container > .gauge > .dial {
  stroke: rgba(255, 255, 255, 0.2);
  stroke-width: 2;
}
.card.bg-gradient > .gauge-container > .gauge > .value {
  stroke: rgba(255, 255, 255, 0.5);
  stroke-width: 4;
}

.card.bg-gradient > .gauge-container > .gauge > .value-text {
  fill: #ffffff;
  font-family: "Montserrat", sans-serif !important;
  font-size: 12px;
  font-weight: 400;
}
